import React from 'react';
import Demo from './demo';
import ButtonAppBar from './toolbar';
import Default from './demo.json';

var the_default = Default;
var getData = localStorage.getItem('MyJson');
if (getData) {
  the_default = JSON.parse(getData);
}

// import Default from './demo.json';
// import FileSaver from 'file-saver';

export function download(data, fileName) {
  if (fileName) {
    var a = document.createElement("a");
    var file = new Blob([JSON.stringify(data)], {type: 'application/json'});
    var filename = prompt("Define your file name.");
    a.href = URL.createObjectURL(file);
    a.download = filename;
    a.click();
  }
}

export function openFileDialog (accept, callback) {  

// Create an input element
var inputElement = document.createElement("input");

// Set its type to file
inputElement.type = "file";

// Set accept to the file types you want the user to select. 
// Include both the file extension and the mime type
inputElement.accept = accept;

// set onchange event to call callback when user has selected file
inputElement.addEventListener("change", callback)

// dispatch a click event to open the file dialog
inputElement.dispatchEvent(new MouseEvent("click")); 
}

export function checkshort(MyJson, shortcut) {
  var final_finded = false;
  for (const cate of Object.keys(MyJson.category)) {
    var finded = MyJson.category[cate].findIndex(item => {
      return item.shortcut === shortcut;
    })
    console.log(finded);
  if (finded  !== -1) {
    console.log(cate);
    final_finded = [cate, finded];
    break;
  }
  }
  return final_finded;
}


export default function App() {

  const [MyJson, setMyJson] = React.useState(the_default);
  // var edopen = false;
  const [edopen, setEdOpen] = React.useState(false);
  const [current_cate_short, setCurrent_cate_short] = React.useState([]);
  const [main, setMain] = React.useState(" ");

  const load_local = () => {
    var getData = localStorage.getItem('MyJson');
    if (getData) {
    setMyJson(JSON.parse(getData));
    }
  }

  const save_local = () => {
    const data =  JSON.stringify(MyJson);
    localStorage.removeItem('MyJson')
    localStorage.setItem('MyJson', data);
  }

  const demo_MyJson = (e) => {
    console.log('change to:' + e)
    setMyJson(e);
  }

  const demo_EdOpen = (e)  => {
    setEdOpen(e);
  }

  const demo_current_cate_short = (e) => {
    setCurrent_cate_short(e);
  }

  const demo_main = (e) => {
    setMain(e);
  }

  const loadfile = (e) => {
    const reader = new FileReader();
    reader.onload = function (e) {
      var result = JSON.parse(e.target.result);
      setMyJson(result);
    };
    reader.readAsText(e.target.files[0]);
  }

  const add_template = (shortname) => {
    if (checkshort(MyJson, shortname)) {
      alert('the shortcut is exist');
      return
    }else if (!shortname) {
      return
    }
    setCurrent_cate_short(['', shortname]);
    setMain('');
    setEdOpen(true);
  }

  const add_category = (cate) => {
    if (cate in MyJson.category) {
      alert('the category is exist');
      return false;
    }
    var temp = MyJson;
    temp.category[cate] = [];
    temp.category_list.push(cate);
    save_local();
    setMyJson(temp);
    return true;
  }

  const setDefault = () => {
    var ans = window.confirm('Are you sure to go back to default set, all local data will lost?');
    if (ans) {
      setMyJson(Default);
      save_local();
    }
  }

  const del_template = (shortcut) => {
    const result = checkshort(MyJson, shortcut);
    // console.log(result);
    if (result) {
      var temp = MyJson;
      temp.category[result[0]].splice(result[1],1);
      setMyJson(temp);
      save_local();
    }else {
      alert('the shortcut is not exist');
    }
  }

  const handleSave = (e) => {
    if (e === 'S') {
      download(MyJson, 'mytemplate.json');
    }else if (e === 'L') {
      openFileDialog('.json', loadfile);
    }else if (e === 'T') {
      var shortname = prompt("Define your template shortcut.");
      add_template(shortname);
    }else if (e === 'C' ){
      var catename = prompt("Define your category name.");
      const result = add_category(catename);
    }else if (e === 'SL' ){
      save_local();
    }else if (e === 'LL' ){
      load_local();
    }else if (e === 'SD' ){
      setDefault();
    }else if (e === 'DT' ){
      var shortname = prompt("Input shoutcut you want to delete.");
      del_template(shortname);
    }else{
      console.log('Errrr')
    }
  }

  return (
    <div className="App">
      <header className="App-header">
        <ButtonAppBar action={handleSave} />
        <Demo MyJson={MyJson} setMyJson={demo_MyJson} edopen={edopen} setEdOpen={demo_EdOpen}
        setCurrent_cate_short={demo_current_cate_short} current_cate_short={current_cate_short}
        setMain={demo_main} main={main} save={save_local} />
      </header>
    </div>
  );
}

