import React from 'react';
import Grid from '@material-ui/core/Grid';

const re = /(G\d{.*?}|M.?{.*?}|m.?{.*?}|H{.*?}|h{.*?}|V{.*?}|v{.*?}|L{.*?}|C{.*?}|c{.*?}|#{.*?}|\r?\n)/;
const regexp = /(^G\d|^M.?|^m.?|^H|^h|^V|^v|^L|^#|^C|^c){(.+?)}/;
// const sub_getop_regexp = /H\[(.?)\]|M\[(.?)\]|LE\[(.?)\]|C\d*\[(.?)\]|L\d*\[(.?)\]/;
const sub_op_regexp = /(H\[.*?\]|M\[.*?\]|LE\[.*?\]|C\d*\[.*?\]|L\d*\[.*?\])/;
const in_regexp = /(H|M|C|LE)\[(.*?)\]/g;
const is_default = /\+(?!\&)/;
const in_blank_regexp = /(L|C)\[(.*?)\]/g;
const in_blank_test = /(L\[.*?\]|C\[.*?\])/;
const in_blank_split = /(L\[.*?\]|C\[.*?\]|\s.+?\s)/;

export function BlinkSent(props) {
  const { blinksents, index } = props;
  const blinks = blinksents.split(in_blank_split);
  console.log(blinks);

  const Blinkcombo = (b_options, id) => {
    b_options = b_options.split('|');

    return(
      <select name={id} >
      {b_options.map((option, option_indxe) => {
        if (option.match(/[+]/g))  {
          option = option.replace(/[+]/g,'')
          return (<option key={id} value={option} selected={true}> {option} </option>);
        }else{
          return (<option key={id} value={option}> {option} </option>);
        }
      })}
      </select>
    );
  }

  return(
    <Grid
    container
    spacing={1}
    >
      {blinks.map((a_blink, b_index) => {
        let b_check = in_blank_regexp.exec(a_blink);
        console.log(b_check);
        const id = `${index}_b${b_index.toString()}`;
        if (b_check) {
          switch(b_check[1]) {
            case 'L':
              return(<Grid key={'B' + id} item ><input key={id} defaultChecked name={id} 
              type="text" /> </Grid>);
            case 'C':
              return(<Grid key={'B' + id} item ><input key={id} defaultChecked name={id} 
              type="text" /> </Grid>);
            default:
              return(<Grid key={'B' + id} item > </Grid>);
          };
        }else{
          return(<Grid key={'B' + id} item > <label htmlFor={'B' + id}> {a_blink} </label> </Grid>);
        }

      })}

    </Grid>
  );
}


export function InVOptions(props) {
    const { options, index, g_index, form_value } = props;
  
    return(
      <Grid
      container
      direction="row"
      spacing={1}
      >
        {options.map((option, option_indxe) => {
          const id = g_index + '_' + option_indxe;
            if (option === 'blank') {
              return (<Grid key={id} item xs> <label> </label> </Grid>);
            }else{
            return (<Grid key={id} item xs> <input ref={form_value[index]} name={index} id={'G_' + id} type="checkbox"  value={option}/> <label htmlFor={'G_' + id}> {option} </label> </Grid>);
            }
          }
        )}
      </Grid>
    );
  }

  export function setDefault(matchs_) {

    let temp = [];
    console.log(matchs_);
    matchs_.forEach(match_ => {
      match_ = match_.replace(in_regexp, (in_match, in_type, in_op) => {
        let temp = [];
        in_op.split('|&').forEach(element => {
            if (is_default.test(element)) {
                element = element.replace(is_default,'').replace('+&','+');
                temp.push(element);
            }
        });
        if (temp.length > 0&&in_type === 'H') {
            return temp[0];
        }else if (temp.length > 0&&in_type === 'M') {
            return jointer(temp);
        }else if (in_type === 'LE') {
            return in_op;
        }else {
            return "";
        }
    });
      temp.push(match_);
    });
    return temp.join('');
  }

  export function jointer(list, seper) {
    if (list.length === 1) {
      return list[0];
    }else if (list.length === 0){
      return '';
    }else if (seper){
      return list.join(seper);
    }else if (list.length === 2) {
      return list[0] + ' and ' + list[1];
    }else{
      return list.slice(0,list.length - 1).join(', ') + ' and ' + list[list.length - 1];
    }
  }
  
  export function InOptions(props) {
    const { sub_matchs, label, index, parentype, form_value } = props;
    const [value, setValue] = React.useState(setDefault(sub_matchs));
    //const sub_op_regexp = /H\[(\d+)\]|M\[(\d+)\]/;
    const [check, setCheck] = React.useState(false);
    const style = {
      backgroundColor: 'lightgreen'
    }
  
    let handleChangeValue = (e) => {
      let temp = "";

      sub_matchs.forEach((item, sub_index) => {
        const name = label + '_' +  item + '_' + sub_index.toString();
        if (sub_op_regexp.test(item)) {
          const myitem = document.getElementsByName(name);
          let temps = [];
          myitem.forEach((obj) => {
            if (obj.checked) {
              temps.push(obj.value);
            }else if (item.split('[')[0] === 'LE'&&obj.value) {
              temps.push(obj.value);
            }
          })
          temp = temp + jointer(temps);
        }else{
          temp = temp + item;
        }
      });
      // console.log('changed:' + e.target.name + " to " + temp);
      setCheck(true);
      setValue(temp);
    };
  
    const boxClick = (e) => {
      // console.log(check)
      if (check) {
        setCheck(false);
      }else{
        setCheck(true);
      }
    };
  
    let mainOption = (type) => {
      switch(type) {
        case 'V':
          return (<input key={index + "_" + label} name={index} id={label} value={value} parentype={parentype} 
            type={(parentype === "V"|parentype === "v") ? "radio":"checkbox"} />);
        case 'M':
        case 'L':
        case 'm':
          return (<input key={index + "_" + label} onChange={boxClick} checked={check} name={index} id={label} 
            value={value} parentype={parentype} type={(parentype === "V"|parentype === "v") ? "radio":"checkbox"} />);
        default:
          return (<p> unknow type </p>);
      }
    }
  
    return (
      <Grid
      container
      direction="row"
      spacing={1}
      key={"Gci_" + index}
    >
      <Grid item> {mainOption(parentype)} </Grid>
  
      {sub_matchs.map((sub_match, sub_index) => {
        const id = label + '_' + sub_match + '_' + sub_index.toString();
        console.log(sub_match);
        let sub_check = in_regexp.exec(sub_match);
        if (sub_check) {
          let type = sub_check[1];
          console.log(sub_check[1] + ":" + type);
          switch(type) {
            case 'H':
              return (<Grid key={'G' + id} item ><AnyOptions key={id} handleChangeValue={handleChangeValue}
                 index={id} style={style} in_op={true} form_value={form_value}
                  options={sub_check[2]} type={'H'} /> </Grid>);
            case 'LE':
              return (<Grid key={'G' + id} item > <input key={id} size={4} htmlFor={label} name={id} type="text" onChange={handleChangeValue} /> </Grid>);
            case 'L':
            case 'm':
            case 'M':
              return (<Grid key={'G' + id} item xs ><AnyOptions key={id} handleChangeValue={handleChangeValue}
                index={id} style={style} in_op={true} form_value={form_value}
                 options={sub_check[2]} type={'MR'} /> </Grid>);
            default:
              return (<p> unknow type </p>);
          }
        }else{
            return (<Grid key={'G' + id} item >  <label key={id} htmlFor={label} vaule={sub_match} >{sub_match}</label> </Grid>);
          }
        })
      }
      </Grid>
      );
    }
  
  
  export function AnyOptions(props) {
    var { options, type } = props;
    const { style, index, handleChangeValue, form_value, set_form_value, in_op} = props;
    const in_regexp = /H\[.+?\]|M\[.+?\]|C\[.+?\]/g;
    // const sub_op_regexp = /H\[.?\]|M\[.?\]|LE\[\]|C\d*\[.?\]|L\d*\[.?\]/;
    let g_num = 3;
  
    if (in_regexp.test(options)) {
      options = options.replaceAll(in_regexp, (match) => {
        console.log(match);
        return match.replaceAll("|","|&");
      });
    }

    if (in_op) {
      options = options.split("|&");
    }else{
      options = options.split(/\|(?!\&)/);
    }
  
    if (type[0] === 'G'&&type.length > 1) {
      g_num = type[1];
      type = type[0];
    }
  
    let Grouped = (myArray, chunk_size) => {
      var results = [];
      while (myArray.length) {
          results.push(myArray.splice(0, chunk_size));
      }
      return results;
    }
    console.log(form_value);

    // console.log(type);
    switch (type) {
      case 'H':
        return (
          <Grid
          container
          direction="row"
          style={style}
          key={"Gc_" + index}
        >
            {options.map((option, option_indxe) => {
              const id = index + '_' + option_indxe.toString();
              if (sub_op_regexp.test(option)) {
                option = option.replace(sub_op_regexp,'')
              }
              option = option.replace("\\+","\\Plus");
              if (option.match(/\+/g)) {
                option = option.replace('+','');
                option = option.replace("\\Plus","+");
                return (<Grid key={'G' + id} item ><input key={id} defaultChecked name={index} id={id} type="radio" onChange={handleChangeValue} value={option}/> <label htmlFor={id}> {option} </label> </Grid>);
              }else{
                option = option.replace("\\Plus","+");
                return (<Grid key={'G' + id} item ><input key={id} name={index} id={id} type="radio" onChange={handleChangeValue} value={option}/> <label htmlFor={id}> {option} </label> </Grid>);
              }
            })}
          </Grid>
    );
      case 'V':
        return (
          <Grid
          container
          direction="column"
          style={style}
          key={"Gc_" + index}
        >
            {options.map((option, option_index) => {
              //let sub_op_regexp = /(H\[\d+\]|M\[\d+\]|LE\[\d?\]|C\d*\[\d+\]|L\d*\[\])/g;
              let id = index + '_' + option_index.toString();
              if (sub_op_regexp.test(option)) {
                console.log(option);
                let sub_matchs = option.split(sub_op_regexp);
                return (<InOptions form_value={form_value} key={id} parentype={type} index={index} label={id} sub_matchs={sub_matchs} />)
              }
              option = option.replace("\\+","\\Plus");
              if (option.match(/\+/g)) {
                option = option.replace('+','');
                option = option.replace("\\Plus","+");
                return (<Grid key={'G' + id} item > <input ref={form_value[index]} key={id} defaultChecked name={index} id={id} type="radio" value={option}/> <label htmlFor={id}> {option} </label> </Grid>);
              }else{
                option = option.replace("\\Plus","+");
                return (<Grid key={'G' + id} item > <input ref={form_value[index]} key={id} name={index} id={id} type="radio" value={option}/> <label htmlFor={id}> {option} </label> </Grid>);
              }
            })}
          </Grid>
    );
      case 'L':
      case 'm':
      case 'M':
        return (
          <Grid
          container
          direction="column"
          key={"Gc_" + index}
        >
            {options.map((option, option_index) => {
              const id = index + '_' + option_index.toString();
              if (sub_op_regexp.test(option)) {
                console.log(option);
                let sub_matchs = option.split(sub_op_regexp)
                // console.log(sub_matchs);
                return (<Grid  key={'G' + id} item > <InOptions form_value={form_value} key={id} parentype={type} index={index} label={id} sub_matchs={sub_matchs} /></Grid>)
              }else{
                return (<Grid key={'G' + id} item > <input ref={form_value[index]} key={id} name={index} id={id} type="checkbox"  value={option}/> <label htmlFor={id}> {option} </label> </Grid>);
              }
            })}
          </Grid>
        );
      case 'G':
        if (g_num > 12) {
          g_num = 12;
        }
        const groups = Grouped(options, g_num);
  
        return (
          <Grid
          container
          direction="column"
          key={"Gc_" + index}
          >
            {groups.map((group, g_index) => {
              const id = index + '_' + g_index.toString();
              return(<Grid key={'G' + id} item> <InVOptions form_value={form_value} key={id}  label={id} options={group} index={index} g_index={g_index} />  </Grid>);
            })}
            
          </Grid>
        );
      case 'MR':
        return (
            <Grid
            container
            direction="row"
            style={style}
            key={"Gc_" + index}
          >
              {options.map((option, option_indxe) => {
                const id = index + '_' + option_indxe.toString();
                return (<Grid key={'G' + id} item xs> <input ref={form_value[index]} key={id} name={index} onChange={handleChangeValue} id={id} type="checkbox"  value={option}/> <label htmlFor={id}> {option} </label> </Grid>);
              })}
            </Grid>
          );
      case 'c':
        return (
            <Grid
            container
            direction="column"
            style={style}
            key={"Gc_" + index}
            >
              <select ref={form_value[index]} name={index} >
              {options.map((option, option_indxe) => {
                const id = index + '_' + option_indxe.toString();
                if (option.match(/[+]/g))  {
                  option = option.replace(/[+]/g,'')
                  return (<option key={id} value={option} selected={true}> {option} </option>);
                }else{
                  return (<option key={id} value={option}> {option} </option>);
                }
              })}
              </select>
            </Grid>
          );
      default:
        console.log('key not found');
        return (<p key={"p_" + index} > unknow type </p>);
    }
  }

export function Makeoption(props) {
    const { main, form_value, set_from_value } = props;
    let matches = [...main.split(re)];

    return(
        <form id='main'>
            <Grid
            container
            direction="column"
            spacing={1}
            key={"G_Main"}
            >
            {matches.map((match, index) => {
              let check = regexp.exec(match);
              if (check) {
                return (<Grid key={"G_" + index.toString()} item> <AnyOptions form_value={form_value} key={"A_" + index.toString()} index={index.toString()} options={check[2]} type={check[1]}/> </Grid>);
              }else{
                let in_blink = in_blank_test.test(match);
                if (in_blink) {
                  return (<Grid key={"G_" + index.toString()} item> <BlinkSent id={index.toString()} key={"L" + index.toString()} index={index.toString()} blinksents={match} /> </Grid>);
                }else{
                  return (<Grid key={"G_" + index.toString()} item> <label id={index.toString()} key={"L" + index.toString()}> {match} </label> </Grid>);
                }
              }
            })}
            </Grid>
        </form>
    );
}