import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';


export const useStyles = makeStyles({
  root: {
    height: 240,
    flexGrow: 1,
    maxWidth: 400,
  },
});


export default function DemoTreeView(props) {
  const { MyJson, openDia } = props;
  const classes = useStyles();
  const handleclick = (e, value, cate_short) => {
    // console.log('the target: ' + Object.keys(e.target.dataset));
    openDia(value, cate_short);
  };

  

  const Tson_tree = MyJson.category_list.map((cate) => {
    return (
      <TreeItem key={cate} nodeId={cate} label={cate}>
        {MyJson.category[cate].map((item, index) => {
          return <TreeItem key={index} main={item.main} onClick={event => handleclick(event, item.main, [cate, item.shortcut])} nodeId={cate + '_' + index} label={item.shortcut}/>
        })}
      </TreeItem>
      );
  })

  
  return (
    <TreeView
      className={classes.root}
      defaultCollapseIcon={<ExpandMoreIcon />}
      defaultExpandIcon={<ChevronRightIcon />}
      // onNodeSelect = {handleclick}

    >
      
      {Tson_tree}
    </TreeView>
  );
}