import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';

const instruction = 'APPchrome extension: \n \
1. : H{option|option|option}"H""|"("{""}")\n \
2. "H{}""V{}":"M{}":()"m{}":()"L{}":()"C":\n \
3. ( "V""M""m""L")M{H[right|left] option|option|option}"H""|"("[""]")\n \
4. "H[]":"M[]":"LE[]":\n \
5. "Hotstring"shortcut"\\"(Enter)template\n \
6. categorytemplatetemplateshortcutshortcut(bug XD)"EDIT"template"CANCEL""OK"\n \
7. menutemplatecategoryAPP\n \
8. bug';

const instructions = instruction.split('\n');

const get_instruction = () => {
  alert(instruction);
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));


export default function ButtonAppBar(props) {
  const { action } = props;
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleMenu = (e, act) => {
    action(act);
    setAnchorEl(null);
  }

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon onClick={handleClick} />
            <Menu
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <MenuItem onClick={(e) => handleMenu(e, 'SL')}>Save Locally</MenuItem> 
              <MenuItem onClick={(e) => handleMenu(e, 'T')}> Add new template </MenuItem>
              <MenuItem onClick={(e) => handleMenu(e, 'C')}> Add new category </MenuItem>
              <MenuItem onClick={(e) => handleMenu(e, 'S')}> Download file </MenuItem>
              <MenuItem onClick={(e) => handleMenu(e, 'L')}> Upload file </MenuItem>
              <MenuItem onClick={(e) => handleMenu(e, 'DT')}> Delete template </MenuItem>
              <MenuItem onClick={(e) => handleMenu(e, 'DC')}> Delete category </MenuItem>
              <MenuItem onClick={(e) => handleMenu(e, 'SD')}> Set to Default </MenuItem>
              
            </Menu>
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            Template
          </Typography>
          <Button color="inherit" onClick={get_instruction}>  </Button>
        </Toolbar>
      </AppBar>
    </div>
  );
}