import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import DemoTreeView from './treeview';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Grid from '@material-ui/core/Grid';
import { Makeoption } from './makeOption';

const in_blank_test = /(L\[.*?\]|C\[.*?\])/;

export function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

export function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

export const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
    height: 400,
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  },
}));


export default function VerticalTabs(props) {
  const { MyJson, setMyJson, edopen, setEdOpen, current_cate_short, setCurrent_cate_short, main, setMain, save} = props;
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const [open, setOpen] = React.useState(false);
  const [form_value, set_form_value] = React.useState({});

  const off_tyText = () => {
    var target = document.getElementsByName("ins_text");
    target.forEach(obj => {
      obj.display = 'none';
    })
  }

  const shortCuts = (key) => {
    for (const [cate, value] of Object.entries(MyJson.category)) {
      var result = value.find(item => {
        return item.shortcut === key;
      })
      if (result) {
        return result
      }
    }
  }; 

  const handleKey = (event) => {
    var startPosition = event.currentTarget.selectionStart;
    var endPosition = event.currentTarget.selectionEnd;
    if (!startPosition === endPosition) {
      console.log('position inequal');
      return
    }
    if (event.key === '\\') {
      // console.log('here is it in: ' + startPosition);
      const text = event.currentTarget.value;
      const re_word = /\b\b/g;
      var keyword = '';
      var begin = startPosition - 2;
      while (begin >= 0 && re_word.test(text.charAt(begin))) {
        keyword = text.charAt(begin) + keyword;
        begin --;
        }

      // console.log('selection:' + begin + 'to' +  startPosition + 'with' + keyword);
      var find_key = shortCuts(keyword);
      if (find_key) {
        event.currentTarget.selectionStart = begin + 1;
        event.currentTarget.selectionEnd = startPosition;
        if (!re.test(find_key.main)) {
          console.log(find_key.main);
          insertText(find_key.main);
          return
        }
        handleClickOpen(find_key.main, keyword)
      }

    }
  }

  const handleClickOpen = (value, cate_short) => {
    setCurrent_cate_short(cate_short);
    // console.log(cate_short);
    setMain(value);
    setOpen(true);
  }

  const handleClose = () => {
    setOpen(false);
  }

  const jointer = (list, seper) => {
    // console.log(list);
    if (list.length === 1) {
      return list[0];
    }else if (seper){
      return list.join(seper);
    }else if (list.length === 2) {
      return list[0] + ' and ' + list[1];
    }else{
      return list.slice(0,list.length - 1).join(', ') + ' and ' + list[list.length - 1];
    }
  };

  function insertText(text) {
    // document.getElementById("context").value= text;
    var target = document.getElementById("context")
    var startPosition = target.selectionStart;

    if (target.setRangeText) {
     //if setRangeText function is supported by current browser
      target.setRangeText(text)
    } else {
      target.focus()
      document.execCommand('insertText', false /*no UI*/, text);
    }
    const the_end = startPosition + text.length;
    target.selectionStart = the_end;
    target.selectionEnd = the_end;
    target.focus();
}

  const editOpen = () => {
    setOpen(false);
    setEdOpen(true);
  }

  const editClose = () => {
    setEdOpen(false);
  }

  const editSave = () => {
    const myEdit = document.getElementById('editext');
    const cateCombo = document.getElementById('cate');
    var temp = MyJson;
    console.log(current_cate_short);
    if (current_cate_short[0]&&!current_cate_short[0] === cateCombo.value) {
      alert('the category will be changed');
      var the_index = temp.category[current_cate_short[0]].find((item) => {
        return item.shortcut === current_cate_short[1];
      })
      temp.category[cateCombo.value].push(temp.category[current_cate_short[0]][the_index]);
      delete temp.category[current_cate_short[0]][the_index];
    }else if (!current_cate_short[0]) {
      const item = {'shortcut':current_cate_short[1],
                    'main':myEdit.value,
                    'description':'',
                    'active':'true'
      }
      temp.category[cateCombo.value].push(item);
    }else{
      temp.category[current_cate_short[0]].forEach((item, index) => {
        if (item.shortcut === current_cate_short[1]) {
          temp.category[current_cate_short[0]][index].main = myEdit.value;
        }
      })
    }
    setMyJson(temp);
    save();
    setMain(myEdit.value);
    setEdOpen(false);
    setOpen(true);
  }

  const handlecopy = (e) => {
    const myEdit = document.getElementById('context');
    myEdit.select();
    document.execCommand('copy');
    myEdit.select(false);
  }

//  const split_lable = (text, index) => {
//    const sents = text.split(/\r?\n/g);
//    sents.map(sent => {
//      console.log(sent);
//      return (<label key={index.toString()}> {sent} </label>);
//    })
//  }

  const handleSubmit = (event) => {
    const re_block = /block\[(\d+)\]/g;
    const myForm = document.getElementById('main');
    const data = new FormData(myForm);
    console.log(form_value);
    let result = {};
    for(var pair of data.entries()) {
      if (pair[0] in result&&pair[0].indexOf('_') === -1) {
        result[pair[0]].push(pair[1]);
      }else if (pair[0].indexOf('_') === -1){
        result[pair[0]] = [pair[1]];
      };
    };
    // console.log(Object.keys(result)[0]);
    let temp = '';
    matches.forEach((item, index) => {
      if (regexp.test(item)) {
        if (index in result) {
          if (item[0] === 'm') {
            temp = temp + jointer(result[index], ' ');
          }else if (item[0] === 'L') {
            temp = temp + jointer(result[index], '\n');
          }else{
            temp = temp + jointer(result[index]);
          }
        }
      }else if(in_blank_test.test(item)) {

      }else{
        temp = temp + item
      }
    })
    if (re_block.test(temp)) {
      temp = temp.replace(re_block, (match, sub_number) => {
        const block_index  = Object.keys(result)[sub_number];
        if (block_index) {
          return result[block_index];
        }else{
          return match;
        }
      });
    }
    // console.log(temp)
    insertText(temp)
    setOpen(false);
  }

  //const handleChange = (event, newValue) => {
  //  setValue(newValue);
  //};

  let re = /(G\d{.+?}|M.?{.+?}|m.?{.+?}|H{.+?}|h{.+?}|V{.+?}|v{.+?}|L{.+?}|C{.+?}|c{.+?}|#{.+?}|\r?\n)/g;
  let regexp = /(^G\d|^M.?|^m.?|^H|^h|^V|^v|^L|^#|^C|^c){(.+?)}/g;
  // let return_re = /(\n(?=(\n+)))+/g

  var textAreaStyle = {
    width: 400,
    height: 450,
    border: '3px solid #cccccc',
    padding: '5px',
    overflow: 'auto',
    marginLeft: '10px'
}

  let matches = [...main.split(re)];
  // document.addEventListener("keypress", handleKey, false);


  return (
    <div className={classes.root}>
      <Grid
        container
        direction="row"
      >
        <Grid item1='true'>
          <DemoTreeView expanded={value + 1} openDia={handleClickOpen} MyJson={MyJson}/>
        </Grid>
        <Grid item2='true'>
          <textarea style={textAreaStyle} onKeyUp={handleKey} id='context'>
          </textarea>
          <Button id='bottum' onClick={handlecopy}> To clipboard </Button> <br />
          <Grid>
          </Grid>
        </Grid>
      </Grid>
      <Dialog open={open} onClose={handleClose} aria-labelledby="Selector-dialog">
        <DialogTitle id="Selector-dialog">Selector</DialogTitle>
        <DialogContent >
          <Makeoption main={main} form_value={form_value} set_form_value={set_form_value} />
        </DialogContent>
        <DialogActions>
          <Button onClick={editOpen} color="primary">
            Edit
          </Button>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSubmit} color="primary">
            OK
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog open={edopen} onClose={editClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Editor</DialogTitle>
          <DialogContent >
          <Grid
            container
            direction="column"
            spacing={2}
            >
            <Grid item>
            <label>Cateegory: </label>
            <select name='cate' id='cate' defaultValue={current_cate_short[0]} >
              {MyJson.category_list.map((item,index) => {
                return(<option key={index} value={item} > {item} </option>);
              })}
            </select>
            </Grid>
            <Grid item>
            <textarea style={textAreaStyle} id='editext' defaultValue={main}>
            </textarea>
            </Grid>
          </Grid>
          </DialogContent>
        <DialogActions>
          <Button onClick={editClose} color="primary">
            Cancel
          </Button>
          <Button onClick={editSave} color="primary">
            SAVE
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}